import React from 'react';
import { Redirect } from "react-router-dom";
import { Player } from "video-react";

import artworkMap from 'fixture/artworkMap';

const Artwork = ({ match }) => {
  const { params } = match;
  const { artwork } = params;

  const artworkData = artworkMap[artwork];

  if (!artworkData) {
    return <Redirect to='/'/>
  }

  const {
    contentImages,
    contentVideos,
  } = artworkData;

  return (
    <div>
      <div className="artwork">
        {contentImages && 
          contentImages.map(image => (
            <div className="artwork-item" key={image}>
              <img src={require(`media/image/${image}`)} alt='artwork' />
            </div>
          ))
        }

        {contentVideos && contentVideos.map(({ poster, video }) => (
          <div className="artwork-item" key={video}>
            <Player
              // playsInline
              fluid={false}
              // width={1205}
              // height={678}
              className='artwork-item__video'
              poster={require(`media/image/${poster}`)}
              src={require(`media/videos/${video}`)}
            />
          </div>
        ))}
      </div>
    </div>
  )
}

export default Artwork;


